	module.exports = {"data":{"markdownRemark":{"html":"<p>The require() function performs the same as include() except for error handling.</p>\n<p>Include() will generate a warning and continue to execute while require() will generate a fatal error and stop.</p>\n<p>The require() and require<em>once() functions are the same. The require</em>once() function will check to see if it has already been included, require() won't check.</p>","frontmatter":{"title":"Include VS Require","date":"July 22nd, 2018"}}},"pathContext":{"slug":"/blog-posts/php/include-vs-require/"}}

